import { memo, useContext, useCallback, useState } from "react";

import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import Typography from "@mui/material/Typography";
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import { BarcodeSvg } from "../common/images/barcode";


import { GlobalsContext } from "../../contexts/globals";
import { appLogger } from "../../utilities/app-logs/app-logger";
import { ImageButton } from "../common/image-button/image-button";
import { MultiSkuSvg } from "../common/images/multi-sku";
import { styles } from "./case-type.styles";

interface CaseTypeModalProps {
  open: boolean;
  onClose: () => void;
}
/**
 * modal to render regulated item function
 */
export const CaseTypeModal = memo(({ open, onClose }: CaseTypeModalProps) => {
  const { globals, setGlobals } = useContext(GlobalsContext);
  const [skuType, setSkuType] = useState("");

  const onConfirm = useCallback(() => {
    appLogger.action("Bottom-Drawer: multi sku button clicked");
    setGlobals({ ...globals, multiSku: skuType === 'multi' ? true: false});
    onClose()
  }, [globals, setGlobals, skuType, onClose]);


  const onClickSkuType = (value: string) => {
    setSkuType(value);
  }

  return (
    <Dialog
      open={open}
      onClose={onClose}
      aria-labelledby="regulated-item-modal-title"
      aria-describedby="regulated-item-modal-description"
    >
      <DialogTitle id="alert-dialog-title">
        <Typography variant="h6" component="div">
          Single or Multi SKU
        </Typography>
        <Typography variant="caption" component="div">
         Scan UPC if an item is single SKU or select Multi SKU if there are multiple UPCâ€™s
        </Typography>
        {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: "black",
          }}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
      </DialogTitle>
      <DialogContent>
        <ImageButton
          styles={{
            ...styles.imageButton,
            backgroundColor: skuType === 'single'
              ? "rgba(0, 113, 220, 0.5)"
              : "white",
          }}
          onClick={() => {
            setSkuType('single');
          }}
        >
          <Box sx={{ m: 2, mt: 3 }}>
          <BarcodeSvg height={80} width={80} color="black" />
            <Typography
              gutterBottom
              variant="body1"
              component="div"
              sx={{ color: "black", mt: 2 }}
            >
              Single SKU
            </Typography>
          </Box>
        </ImageButton>
        <ImageButton
          styles={{
            ...styles.imageButton,
            backgroundColor: skuType === 'multi'
              ? "rgba(0, 113, 220, 0.5)"
              : "white",
          }}
          onClick={() => {
            onClickSkuType('multi')
          }}
        >
          <Box sx={{ m: 2, mt: 3 }}>
            <MultiSkuSvg height={80} width={80} color="black" />
            <Typography
              gutterBottom
              variant="body1"
              component="div"
              sx={{ color: "black", mt: 2 }}
            >
              Multi SKU
            </Typography>
          </Box>
        </ImageButton>
      </DialogContent>
      <DialogActions>
      <Button variant="text" sx={styles.cancelButton} onClick={onClose}>
            Cancel
          </Button>
          <Button variant="contained" sx={styles.button} onClick={onConfirm}>
            Confirm
          </Button>
        </DialogActions>
    </Dialog>
  );
});
CaseTypeModal.displayName = "CaseTypeModal";
